// eslint-disable-next-line import/no-named-default
import { default as router, whiteList } from "./router";
import store from "./store";
import { getLocalStorage, storages } from "@/utils/auth";
// import { LimitType } from "@/utils/enum";

const toLoginList = ["/login", "/", "/my-zone"]; // 可以跳转登录页的路由

router.beforeEach(async (to, from, next) => {
  // console.log("to:", to);
  // console.log("from:", from);
  store.commit("SET_LOADING", true);
  // 是否已登录
  if (getLocalStorage(storages.userID)) {
    if (to.path === "/login" && toLoginList.indexOf(from.path) === -1) {
      // 已经登录的，限制跳转登录页
      next(false);
    } else {
      next();
    }
  } else {
    /* has no token */
    if (whiteList.indexOf(to.path) > -1) {
      // 路由白名单，直接跳转
      next();
    } else {
      next("/login");
    }
  }
});

router.afterEach((to, from) => {
  store.commit("SET_LOADING", false);
  let toIndex = store.getters.navigations.findIndex(r => r.path === to.path);
  if (toIndex >= 0) { // 存在该路径
    let len = store.getters.navigations.length - 1;
    if (toIndex === len) { // 当前路径是最后一条，证明是同一个页面
      console.log("refresh")
    } else { // 后退
      const navigations = store.getters.navigations.slice(0, toIndex + 1);
      store.commit("SET_ROUTER_DIRECTION", { routerDirection: 'back' }); // 后退标志
      store.commit("SET_NAVIGATIONS", navigations) // 删除当前路径后面的路径
    }
  } else { // 不存在该路径
    const navigations = store.getters.navigations.concat([{
      path: to.path,
      params: to.params,
      query: to.query
    }]);
    store.commit("SET_ROUTER_DIRECTION", { routerDirection: 'forward' }); // 前进标志
    store.commit('SET_NAVIGATIONS', navigations)// 保存该连接
  }
});
