/**
 * set 存储方法
 * @param {*} name 键
 * @param {*} value 值
 * @param{*} expires 超时时间
 */
export function setLocalStorage (name, value, expires) {
  if (!value) {
    localStorage.removeItem(name);
  } else {
    const params = { name, value, expires };
    if (expires) {
      const data = Object.assign(params, { startTime: new Date().getTime() });
      localStorage[name] = JSON.stringify(data);
    } else {
      const data = Object.assign(params);
      localStorage[name] = JSON.stringify(data);
    }
  }
}

/**
 * get 存储方法
 * @param {*} name 键
 */
export function getLocalStorage (name) {
  const item = localStorage[name];
  let object;
  try {
    object = JSON.parse(item);
  } catch (err) {
    return item;
  }

  // 如果有startTime的值，说明设置了失效时间
  if (object && object.startTime) {
    const date = new Date().getTime();
    // 如果大于就是过期了，如果小于或等于就还没过期
    if (date - object.startTime > object.expires) {
      localStorage.removeItem(name);
      return false;
    } else {
      return object.value;
    }
  } else {
    return object.value;
  }
}

/**
 * remove 存储方法
 * @param {*} name 键
 */
export function removeLocalStorage (name) {
  localStorage.removeItem(name);
}

