import axios from "axios";
import store from "@/store";
// eslint-disable-next-line import/no-named-default
import { default as Router, whiteList } from "./../router";
import { Toast } from "vant";
import { getNowDateTime } from "@/utils/index";

const CryptoJS = require("crypto-js");

// eslint-disable-next-line no-undef
const setting = appConfig;

function authorization () {
  if (store.getters.token) {
    const backToken = store.getters.backToken;
    const times = [];
    if (backToken) {
      const rTimeBytes = CryptoJS.AES.decrypt(backToken + "", store.getters.token + "");
      if (rTimeBytes) {
        const rTime = rTimeBytes.toString(CryptoJS.enc.Utf8);
        if (rTime) {
          times.push(rTime);
        }
      }
      times.push(getNowDateTime());
    }
    const timesStr = CryptoJS.enc.Utf8.parse((JSON.stringify(times))); // 要加密的数值
    const iv = CryptoJS.enc.Utf8.parse("0000FJRH95270000"); // 对称算法16位
    const key = CryptoJS.enc.Utf8.parse(store.getters.token.slice(0, 32)); // 密钥
    return CryptoJS.AES.encrypt(timesStr, key, {
      iv: iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7
    }).ciphertext.toString();
  }
  return "";
}

const service = axios.create();
service.defaults.timeout = 1000 * 60; // 请求超时 时长
service.defaults.headers.common['Access-Platform'] = "APP";

service.interceptors.request.use(
  config => {
    // do something before request is sent
    config.baseURL = "http://" + store.getters.serverIP + ":" + setting.mainServerPort;
    if (store.getters.token) {
      config.headers["Access-Token"] = store.getters.token;
      config.headers["Authorization"] = authorization();
    } else {
      if (Router.apps[0]) {
        const nowPath = Router.apps[0]._route.path;
        if (whiteList.indexOf(nowPath) === -1) {
          store.dispatch("Logout");
          location.reload(); // 刷新页面
        }
      }
    }
    // if (store.getters.userID) config.params = { ...config.params, token: store.getters.userID };
    store.commit("SET_REQUEST", 1);
    return config;
  },
  error => {
    console.log("request=>error:", error);
    store.commit("SET_REQUEST", -1);
    return Promise.reject(error);
  }
);

// response interceptor
service.interceptors.response.use(
  response => {
    const res = response.data;
    store.commit("SET_REQUEST", -1);
    let expired = response && response.headers && response.headers["access-token"] === "Login Is Expired";
    expired = expired || (response && response.statusText && response.statusText.indexOf("expired") > -1);
    if (expired) {
      setTimeout(() => {
        Toast("登录已过期，请重新登录");
        store.dispatch("Logout");
        location.reload(); // 刷新页面
      }, 500);
      return { Result: false, Info: "登录已过期，请重新登录~" };
    } else {
      return res;
    }
  },
  error => {
    console.log("response=>error:", error); // for debug
    let expired = error.response && error.response.headers && error.response.headers["access-token"] === "Login Is Expired";
    expired = expired || (error.response && error.response.statusText && error.response.statusText.indexOf("expired") > -1);
    if (error.code === "ECONNABORTED" && error.message.indexOf("timeout") > -1) {
      Toast("请求超时~");
    } else if (error.message.indexOf("Network Error") > -1) {
      Toast("请求失败");
    } else if (expired) {
      Toast("登录已过期，请重新登录~");
      setTimeout(() => {
        store.dispatch('Logout');
        location.reload(); // 刷新页面
      }, 500);
    }
    store.commit("SET_REQUEST", -1);
    return Promise.reject(error);
  }
);

