<template>
  <div>
    <router-view></router-view>
    <div class="footer">
      <van-tabbar v-model="activeIndex">
        <van-tabbar-item v-for="(item, index) in menus" :key="index" @click="handleClick(item)">
          <i slot="icon" class=" iconfont" :class="item.icon"></i>
          <span>{{item.name}}</span>
        </van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { switchType } from "@/utils/enum";
export default {
  name: "index",
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$gatewayHttp.gateway.linkTest().then(res => {
        // console.log("res:", res);
        vm.linkState = 1;
      }).catch(() => {
        vm.linkState = 0;
        Dialog.alert({
          title: "警告",
          message: "找不到当前课程服务！即将退出"
        }).then(() => {
          this.$store.commit("QUIT_COURSE");
          next("/subject/view");
        });
      });
    });
  },
  beforeRouteLeave (to, from, next) {
    let haveCourse = this.$store.getters.courseID && to.path === "/subject/view";
    if (haveCourse && window.GatewayHub && window.GatewayHub.connection.state === 1) {
      Dialog.confirm({
        title: "警告",
        message: "是否强制退出当前课程？"
      }).then(() => {
        // on confirm
        this.$store.commit("QUIT_COURSE");
        next();
      }).catch(() => {
        // on cancel
        // next(false);
      });
    } else {
      next();
    }
  },
  data () {
    return {
      linkState: 0,
      activeIndex: 0,
      menus: [
        { name: "记录", icon: "icon-kejian1", switchType: switchType.Record, url: "/course/record" },
        { name: "交互", icon: "icon-kongzhitai", switchType: switchType.Students, url: "/course/power" },
        { name: "学生", icon: "icon-renmai", switchType: switchType.Students, url: "/course/student-list" }
      ]
    };
  },
  created () {
    this.$gatewaySignalR.startGatewaySignalR();
  },
  methods: {
    handleClick (item) {
      this.$router.replace(item.url).catch(() => {
      });
    },
    reFreshIndex () {
      this.activeIndex = this.menus.findIndex(r => r.url === this.$route.path);
      if (this.menus[this.activeIndex].switchType || this.menus[this.activeIndex].switchType === "0") {
        this.$gatewayHttp.broadcast.switchPage({ function: this.menus[this.activeIndex].switchType });
      }
    }
  },
  watch: {
    $route: {
      handler: "reFreshIndex",
      immediate: true
    }
  }
}
</script>

<style lang="less" scoped>
.footer {
  /deep/ .van-tabbar-item--active {
    color: #00cbab;
  }
}
</style>
