<template>
  <div id="app">
    <r-loading v-show="$store.getters.requestNum > 0"></r-loading>
    <router-view></router-view>
  </div>
</template>
<script>
import RLoading from "./components/r-loading";
import mainSignalR from "@/mixin/mainSignalR";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import { LimitType } from "@/utils/enum";

export default {
  name: "App",
  components: { RLoading },
  mixins: [mainSignalR],
  data () {
    return {
      clientHeight: document.documentElement.clientHeight,
      timeStamp: 0
    };
  },
  mounted () {
    // 如果支持 popstate 一般移动端都支持了
    if (window.history && window.history.pushState) {
      // 往历史记录里面添加一条新的当前页面的url
      history.pushState(null, null, document.URL);

      // 给 popstate 绑定一个方法 监听页面刷新
      window.addEventListener("popstate", this.backChange, false); // false阻止默认事件
      this.$on("hook:destroyed", () => {
        window.removeEventListener("popstate", this.backChange, false);
      });
    }
  },
  methods: {
    backChange (e) {
      const isTop = this.$route.meta && this.$route.meta.limits && this.$route.meta.limits.indexOf(LimitType.top) > -1;
      const backUrl = this.$route.meta ? this.$route.meta.from : "";
      if (isTop) { // 顶级页面连续点击两次返回，则退出应用
        console.log("当前是顶级了，无法再后退");
        window.history.go(1);
        if (e.timeStamp - this.timeStamp < 50) {
          sendToAPP(FuncType.closeAPP, "");
        }
        this.timeStamp = e.timeStamp;
      } else if (backUrl) { // 指定返回页
        this.$router.replace(backUrl);
      } else {
        let len = this.$store.getters.navigations.length;
        if (len > 1 && this.$store.getters.navigations[len - 2].path !== "/login") {
          const { path, params, query } = this.$store.getters.navigations[len - 2];
          this.$router.replace({
            path,
            params,
            query: {
              ...query,
              APP_KEY: Math.random().toString(16).substring(2)
            }
          });
        }
      }
    },
    getAllItem (values) {
      const arr = [];
      values.map(item => {
        if (item.children && item.children.length > 0) {
          const arrs = this.getAllItem(item.children);
          arr.push(...arrs);
        } else if (item.meta && item.meta.cache) {
          arr.push(item.name);
        }
      });
      return arr;
    }
  }
};
</script>

<style lang="less">
@import "../src/styles/index.less";
</style>
