/**
 * 截屏提问类型名称
 * @param {*} val
 */
export function screenQuestionTypeName (val) {
  if (val === 1) {
    return "单选题";
  } else if (val === 2) {
    return "多选题";
  } else if (val === 3) {
    return "判断题";
  } else if (val === 4) {
    return "问答题";
  } else {
    return "未知题型";
  }
}

/**
 * 截屏提问模式名称
 * @param {*} val
 */
export function screenPatternName (val) {
  if (val === 1) {
    return "全员作答";
  } else if (val === 2) {
    return "随机挑人";
  } else if (val === 3) {
    return "抢答模式";
  } else {
    return "其他";
  }
}

