import { getNowDateTime } from "@/utils/index";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import { LoginPlatform } from "@/utils/enum";

export default {
  data () {
    return {
      conn: null,
      reConnet: false,
      timeStamp: 0
    };
  },
  mounted () {
    this.$nextTick(() => {
      this.startSignalR();
      document.addEventListener("visibilitychange", () => {
        if (!document.hidden) {
          console.log(getNowDateTime(), "激活页面");
          this.reConnet = false;
          this.startSignalR();
        }
      });
    });
  },
  methods: {
    async openSocket () {
      if (!this.$store.getters.serverIP || !this.$store.getters.signalRPort) return;
      // eslint-disable-next-line no-undef
      this.conn = $.hubConnection("http://" + this.$store.getters.serverIP + ":" + this.$store.getters.signalRPort);
      window.HUB = this.conn.createHubProxy("TerminalHub");
      // 如果没有注册的话，Hubs的 OnConnected 方法将不会被调用，那么客户端的方法就不能被服务端调用（这容易埋坑，所以要提前注册方法）。
      this.bindEvent();

      const strValue = this.$store.getters.userID + "," + (window.webkit ? LoginPlatform.TeacherIos : LoginPlatform.TeacherAndroid);
      this.conn.qs = { LoginInfo: strValue };
      // this.conn.Headers = {
      //   ...this.conn.Headers,
      //   LoginInfo: strValue
      // };
      this.conn.start().done(data => {
        console.log(getNowDateTime(), "SignalR服务启动成功！");
        // window.HUB.invoke("QuitGroup", "All").catch(err => {
        //   console.log("err：", err);
        // });
        // window.HUB.invoke("JoinGroup", "All").catch(err => {
        //   console.log("err：", err);
        // });

        window.HUB.invoke("QuitGroup", this.$store.getters.teacherID).catch(err => {
          console.log("err：", err);
        });
        window.HUB.invoke("JoinGroup", this.$store.getters.teacherID).catch(err => {
          console.log("err：", err);
        });
      }).fail(err => {
        console.log("signalR服务连接失败！", err);
      });

      this.conn.disconnected(() => {
        console.log(getNowDateTime(), "signalR服务断开");
      });

      // stateChanged：连接状态更改时引发。 提供的旧状态和新的状态 （连接、 已连接、 正在重新连接或已断开连接）。
      this.conn.stateChanged((data) => {
        console.log("data:", data);
        if (data.newState !== 1) {
          // this.$store.commit("SET_INIT_STATE", false);
          this.startSignalR();
        } else {
          console.log("SignalR服务已连接上");
          this.signalRConnected();
        }
      });
    },
    bindEvent () {
      window.HUB.on("ReceiveGrantLogin", value => {
        this.receiveLinkSuccess(value);
      });
    },
    /**
     * 跳转链接
     * @param toPath 即将跳转的地址
     */
    turnPath (toPath) {
      sendToAPP(FuncType.toPath, toPath);
      if (this.$route.path.indexOf(toPath) === -1) {
        this.$router.replace(toPath);
      }
    },
    clearInstance () {
      if (this.timer) {
        window.clearInterval(this.timer);
      }
    },
    startSignalR () {
      if (!this.$store.getters.userID) return;
      if (!this.reConnet) {
        console.log(getNowDateTime(), "请求启动signalR");
        this.reConnetServer();
        this.reConnet = true;
      }
    },
    reConnetServer () {
      try {
        if (!this.conn || this.conn.state !== 1) {
          console.log(getNowDateTime(), "检测到signalR服务未连接");
          this.openSocket();
          setTimeout(() => {
            this.reConnetServer();
          }, 3000);
        } else {
          console.log("通信服务连接成功，关闭重连事件", this.conn.state, this.conn);
          this.reConnet = false;
        }
      } catch (e) {
        setTimeout(() => {
          this.reConnetServer();
        }, 3000);
      }
    },
    /**
     * signalR服务启动成功后执行的程序
     */
    signalRConnected () {
      // this.$store.commit("SET_REGETKEY", true);
    },
    /**
     * PC登录成功
     * @param value
     */
    receiveLinkSuccess (value) {
      // {"ClassroomID":"18090618462577194701043071384023","CIID":"20050716382857099701464045495766"}
      console.log("receiveLinkSuccess:", value);
      this.getCourseInfo();
    },
    getCourseInfo () {
      this.$mainHttp.classInfo.inClassInfo({
        chID: this.$store.getters.classHourID,
        classID: this.$store.getters.classID,
        teacherID: this.$store.getters.teacherID
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          const course = {
            id: res.Datas[0].Key["CIID"],
            classHourID: res.Datas[0].Key["CHID"],
            classID: res.Datas[0].Key["ClassID"],
            classRoomID: res.Datas[0].Key["ClassroomID"],
            classRoomName: res.Datas[0]["ClsRoomName"],
            teacherID: res.Datas[0].Key["TeacherID"],
            classDate: res.Datas[0].Key["ClassDate"],
            state: res.Datas[0].Key["State"],
            createTime: res.Datas[0].Key["CreateTime"],
            updateTime: res.Datas[0].Key["UpdateTime"]
          };

          this.$store.commit("SET_COURSE", course);
          this.$store.commit("SET_COURSE_ID", course.id);
          this.toStartCourse();
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.toLinkCourse();
        }
      }).catch(() => {
        this.$toast("更新课程信息失败");
      });
    },
    toStartCourse () {
      this.$store.dispatch("GetCourseServiceByUser", this.$store.getters.course.classRoomID).then(res => {
        if (res.Result) {
          this.linkTest(0);
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("获取教室服务失败");
        }
      }).catch(() => {
        this.$toast("获取教室服务出错");
      });
    },
    // 连接测试
    linkTest (index) {
      index++;
      this.$gatewayHttp.gateway.linkTest().then(res => {
        this.$router.push("/course");
      }).catch(() => {
        // 3秒内连接测试失败，作失败处理
        if (index > 6) {
          history.go(0);
        } else {
          setTimeout(() => {
            this.linkTest();
          }, 500);
        }
      });
    }
  },
  watch: {
    "$store.getters.userID" (newVal, oldVal) {
      if (newVal) {
        setTimeout(() => {
          this.startSignalR();
        }, 500);
      } else {
        if (this.conn) this.conn.stop(false, true);
        this.reConnet = false;
      }
    }
    // "$store.getters.needReGetKey" (newVal, oldVal) {
    //   if (newVal !== oldVal) {
    //     if (newVal) {
    //       // 服务器IP或端口修改，则重启signalR服务
    //       if (this.conn.baseUrl !== `http://${this.$store.getters.serverIP}:${this.$store.getters.signalRPort}`) {
    //         this.conn.stop(false, true);
    //         setTimeout(() => {
    //           this.reConnet = false;
    //           this.startSignalR();
    //         }, 500);
    //       }
    //     } else {
    //       this.clearInstance();
    //     }
    //   }
    // }
  },
  destroyed () {
    if (this.conn) this.conn.stop(false, true);
    this.clearInstance();
  }
};
